import React, { useEffect, useState } from "react";
import Recipie from "./Recipe";

const App = () => {
  const [recipes, setRecipes] = useState([]);

  const [search, setSearch] = useState("");

  const [query, setQuery] = useState("ckicken");

  useEffect(() => {
    getRecipes();
  }, [query]);

  const APP_ID = "e21943aa";
  const APP_KEY = "60143df3c8aa19900472e831f19e05ca";

  const getRecipes = async () => {
    const response = await fetch(
      `https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`
    );

    const data = await response.json();
    setRecipes(data.hits);
    console.log(data);
  };

  const updateSearch = (event) => {
    setSearch(event.target.value);
    console.log(search);
  };

  const getSearch = (event) => {
    event.preventDefault();
    setQuery(search);
  };

  return (
    <>
      <h1>The FoodBoy</h1>
      <form onSubmit={getSearch} className="search">
        <input type="text" value={search} onChange={updateSearch} placeholder="Search your desired foods here" />
        <button type="submit">Search</button>
      </form>

      <div className="container">
        <div className="recipes">
          {recipes.map((value, index) => {
            return (
              <Recipie
                key={index}
                id={index}
                title={value.recipe.label}
                calories={value.recipe.calories}
                items={value.recipe.ingredients}
                img={value.recipe.image}
              />
            );
          })}
        </div>
      </div>

      <div className="copyright">
        <p>Ajmin Pradhan &copy; 2020</p>
      </div>

    </>
  );
};

export default App;
